export const SEND_MESSAGE_MODAL_TOGGLE = "MODALS/SEND_MESSAGE_MODAL_TOGGLE";
export const SEND_MESSAGE_MODAL_TOGGLER = "MODALS/SEND_MESSAGE_MODAL_TOGGLER";

export const setSendMessageModalToggler = (payload) => (dispatch) => {
    dispatch({ type: SEND_MESSAGE_MODAL_TOGGLER, payload })
}
export const toggleSendMessageModal = (payload = {}) => (dispatch, getState) => {
    getState()?.modals?.SEND_MESSAGE_MODAL_TOGGLER()
    dispatch({ type: SEND_MESSAGE_MODAL_TOGGLE, payload })
}

const initialState = {
    SEND_MESSAGE_MODAL: false,
    SEND_MESSAGE_MODAL_TOGGLER: () => {

    }
}
export default (state = initialState, action) => {
    switch (action.type) {
        case SEND_MESSAGE_MODAL_TOGGLE:
            return {
                ...state,
                SEND_MESSAGE_MODAL: !state.SEND_MESSAGE_MODAL
            }
        case SEND_MESSAGE_MODAL_TOGGLER:
            return {
                ...state,
                SEND_MESSAGE_MODAL_TOGGLER: action?.payload
            }
        default:
            return state;
    }
};
