import React, { Component } from 'react';
import _ from 'lodash';
import { connect } from 'react-redux';
import globalStyles from '../../../common/style';
import { withRouter } from "react-router";
import moment from 'moment';
import { getPosts } from '../../../modules/blogs';
import { toggleSendMessageModal } from '../../../modules/static/modals';
import Twitch from '../../../components/Twitch';
import CoursesData from '../../../data/courses';
import StarValue from '../../../components/StarValue';


class HomeScreen extends Component {
    state = {
        blogs: [],
        loaded: false
    }
    constructor(props) {
        super(props);
    }
    componentDidMount() {

        if (this.props.posts.length === 0) {
            this.props.getPosts()
        }
        setTimeout((() => {
            this.setState({ loaded: true })
        }).bind(this), 1000)
    }
    toggleToast(id, change = null) {
        const changeArr = ["show", "hide"];
        const element = document.getElementById(id);
        if (change !== null) {
            const changeIndex = changeArr.indexOf(change)
            const otherIndex = 1 - changeIndex;
            element.classList.remove(changeArr[otherIndex])
            element.classList.add(changeArr[changeIndex])
            return true;
        }
        if (element) {
            if (element.classList.contains("hide")) {
                element.classList.remove("hide")
                element.classList.add("show")
            } else {
                element.classList.remove("show")
                element.classList.add("hide")
            }
        }
        return true;
    }
    renderToastSection() {
        return (
            <div>
                {/* <button type="button" onClick={() => this.toggleToast("toastNumber1")} className="btn btn-primary" id="liveToastBtn">Show live toast</button> */}
                <div className="position-fixed bottom-0 end-0 p-3" style={{ zIndex: 11 }}>
                    <div id="liveToast" className="toast hide" id="toastNumber1" role="alert" data-bs-autohide="true" aria-live="assertive" aria-atomic="true">
                        <div className="toast-header">
                            <img src="https://avatars.githubusercontent.com/u/48398625?v=4" width="30px" height="30px" className="rounded me-2" alt="..." />
                            <strong className="me-auto">Bootstrap</strong>
                            <small>11 mins ago</small>
                            <button type="button"
                                className="btn-close"
                                data-bs-dismiss="toast"
                                aria-label="Close"
                                onClick={() => this.toggleToast("toastNumber1", "hide")}>

                            </button>
                        </div>
                        <div className="toast-body">
                            Hello, world! This is a toast message.
                            Hello, world! This is a toast message.
                            Hello, world! This is a toast message.
                            Hello, world! This is a toast message.
                            Hello, world! This is a toast message.
                            Hello, world! This is a toast message.
                        </div>
                    </div>
                </div>
            </div>
        )
    }
    renderBackgroundImageSection() {
        return (
            <section className="container initial-section"
                id="section-home"
                data-stellar-background-ratio="0.5">
                <div className="intro-text">
                    <div className="text-center">
                        <h1 className="initial-header">
                            <strong className="header-title">Can
                                Gökçeaslan</strong>
                        </h1>
                        <h2 className="lead site-subheading mb-4">
                            <strong>Hedeflerine
                                fazlandırarak
                                ulaşmayı seven bir
                                teknoloji ve
                                bilim
                                meraklısıyım. Başkalarını etkileyebilmek ve dünyayı daha
                                iyi bir yer haline getirebilmek için çalışarak motive oluyorum. <br /><br />
                                <a target="__blank"
                                    onClick={this.props.toggleSendMessageModal}
                                    className="reach"
                                    id="tikla">
                                    Bana Hemen Ulaş !
                                </a>
                            </strong>
                        </h2>
                    </div>
                </div>
            </section>
        )
    }
    renderAboutSection() {
        return (
            <section className="about-section" id="about-section">
                <div className="container">
                    <div className="row mb-5 align-items-center justify-content-center">
                        <div className="col-md-12 mb-5">
                            <div className="section-heading text-center">
                                <h2 style={{
                                    letterSpacing: "-0.5px !important",
                                    fontWeight: "bold"
                                }}>Ben Kimim?</h2>
                            </div>
                        </div>
                        <div className="image col-lg-5 col-md-12 pr-lg-5 mb-5 mb-lg-5">
                            <img src="assets/webp/sub-image-cropped-final-minified.webp"
                                alt="Can Gökçeaslan Uzun Fotoğraf"
                                className="img-fluid"
                            />
                        </div>
                        <div className="about col-lg-7 col-md-12 pl-lg-7">
                            <p className="about-text" style={{
                                color: "gray",
                                fontWeight: 400,
                                marginTop: "-10px",
                                padding: "0px 10px 0px 10px",
                                letterSpacing: "0px"
                            }}> Merhabalar, ben Can Gökçeaslan. Boğaziçi Üniversitesi
                                Fizik Bölümü 2020 mezunuyum. Fizik bölümü okumamdan dolayı her türlü nesne ve fikiri kolayca modelleme
                                yeteneğine sahibim. Fizik bölümünde okurken Yazılım alanına yönelmem gerektiğini düşündüm ve bu sebeple
                                bölümümüzde açılan Linux ve Python programlama dersini aldım. O günden sonra da kod yazmaktan asla
                                sıkılmadım. Şu anda da Yazılım alanında çalışmaktayım. Özellikle <b>HTML, CSS, JavaScript,
                                    NodeJS, PHP, Python, Java</b> dilleriyle aktif olarak çalıştım. Bunların yanında <b>Android Native,
                                        Bootstrap,
                                        React, React Native,
                                        Socket.io, ElectronJS, ExpressJS ve Redux</b> kullanan projeler geliştirdim. Data Crawling tarafında
                                <b>puppeteer /
                                    pyppeteer, cURL ve Beautiful Soap</b> gibi yapıları aktif olarak kullandım. Analiz ve görselleştirme
                                alanlarında
                                <b> numpy, pandas, matplotlib, plotly </b> gibi yapılarla Fizik bölümünde deney analizleri yaparken çalıştım.
                                Bu
                                sebeple raporlama ve formülsel açıklamalar için yeterli <b>LaTeX</b> de bilmekteyim.
                            </p>
                            <p style={{ textAlign: "left" }}>
                                <a href="https://www.linkedin.com/in/cangokceaslan" target="_blank" title="English CV - Can Gokceaslan" className="btn btn-secondary px-4 py-2 btn-sm" style={{
                                    fontWeight: "bold",
                                    backgroundColor: globalStyles.primaryColor,
                                    borderColor: globalStyles.primaryColor,
                                    marginLeft: "10px",
                                    borderWidth: "0px !important",
                                    letterSpacing: "0.4px",
                                    marginTop: "10px",
                                    textTransform: "none !important",
                                    fontSize: "20px",
                                    color: "white"
                                }}>Linkedin
                                    - Can Gökçeaslan</a>

                            </p>
                        </div>
                    </div>
                </div></section>
        )
    }
    renderInBetweenSection() {
        return (
            <div className="container-fluid in-between">
                <div className="container">
                    <div className="row">
                        <div className="twitch-logo">
                            <img
                                src="assets/svg/twitch.svg"
                                width="150px"
                                height="auto"
                            />
                        </div>
                        <div className="d-flex align-items-center justify-content-center">
                            <div className="padder" style={{ background: "white" }}>
                                <div>
                                    <Twitch />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

            </div>
        )
    }
    renderOneCourse(item = {}) {
        return (
            <div onClick={() => {
                window.open(item?.url, "__blank")
            }} className="col-lg-4 col-md-6 col-12 justify-content-center d-flex" style={{
                cursor: "pointer"
            }}>
                <div className="card courses-card">
                    <img src={item?.thumbnail || "https://mapandan.gov.ph/wp-content/uploads/2018/03/no_image.jpg"} onError={(e) => {
                        e.target.src = "https://mapandan.gov.ph/wp-content/uploads/2018/03/no_image.jpg"
                    }} className="card-img-top" width="100%" height="auto" style={{
                        objectFit: "cover"
                    }} alt="..." />
                    <div className="card-body courses-body">
                        <h1>{item?.title}</h1>
                        <p className="course-author"
                            onClick={() => {
                                window.open(item?.authorUrl, "__blank")
                            }}>
                            {item?.author}
                        </p>
                        <p className="course-stars">{item?.star} <span>{<StarValue value={item?.starValue} />}</span></p>
                        <p className="course-information">{item?.details}</p>
                        <p className="course-price">{item?.price}</p>
                        <p className="course-registered">{item?.registered} kayıt</p>

                    </div>
                </div>
            </div>
        )
    }
    renderCoursePoint(item = {
        url: "https://www.udemy.com/user/cangokceaslan",
        thumbnail: "assets/jpg/course-udemy-resized.jpg",
        title: "Daha Fazla Eğitime Ulaşmak İçin Hemen Ziyaret Et",
        author: "Can Gökçeaslan",
        authorUrl: "https://www.udemy.com/cangokceaslan",
        star: "4.3",
        starValue: 4.3,
        details: "Toplam 8,5 saat - 2 Kurs - Başlangıç",
        price: "Ücretsiz",
        registered: "41.000+"
    }) {
        return (
            <div onClick={() => {
                window.open("https://www.udemy.com/user/cangokceaslan", "__blank")
            }} className="col-lg-4 col-md-6 col-12 justify-content-center d-flex" style={{
                cursor: "pointer"
            }}>
                <div className="card courses-card">
                    <img src={item?.thumbnail} onError={(e) => {
                        e.target.src = "https://mapandan.gov.ph/wp-content/uploads/2018/03/no_image.jpg"
                    }} className="card-img-top" width="100%" height="auto" style={{
                        objectFit: "cover"
                    }} alt="..." />
                    <div className="card-body courses-body">
                        <h1>{item?.title}</h1>
                        <p className="course-author"
                            onClick={() => {
                                window.open(item?.authorUrl, "__blank")
                            }}>
                            {item?.author}
                        </p>
                        <p className="course-stars">{item?.star} <span>{<StarValue value={item?.starValue} />}</span></p>
                        <p className="course-information">{item?.details}</p>
                        <p className="course-price">{item?.price}</p>
                        <p className="course-registered">{item?.registered} kayıt</p>
                    </div>
                </div>
            </div>
        )
    }
    renderLatestCoursesSection() {
        return (
            <section className="courses" id="courses">
                <h2 className="courses-header">
                    <img src="assets/svg/udemy.svg" width="120px" height="40px" style={{ marginRight: "5px" }} />Eğitimlerim
                </h2>
                <div className="container-fluid top-container">
                    <div className="courses-container">
                        <div className="container">
                            <div className="row">
                                {CoursesData.map((course, index) => {
                                    return this.renderOneCourse(course)
                                })}
                                {this.renderCoursePoint()}
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        )
    }
    renderOnePost(item = {}) {
        return (
            <div className="col-lg-4 justify-content-center d-flex">
                <div className="card blog-card">
                    <img onClick={() => window.open(item?.link, "__blank")}
                        src={item?.thumbnail || "https://mapandan.gov.ph/wp-content/uploads/2018/03/no_image.jpg"}
                        onError={(e) => {
                            e.target.src = "https://mapandan.gov.ph/wp-content/uploads/2018/03/no_image.jpg"
                        }}
                        className="card-img-top"
                        width="100%"
                        height="250px"
                        style={{
                            objectFit: "cover"
                        }}
                        alt={item?.title.replace(/(&amp;)/ig, " & ")} />
                    <div className="card-body blog-body">
                        <p className="blog-information d-flex align-items-center justify-content-center">
                            <span style={{ flex: 3 }}>Yazar:
                                <a className="blog-author"
                                    style={{
                                        color: globalStyles.primaryColor,
                                        fontFamily: 'inherit',
                                        fontWeight: 'bold',
                                        marginLeft: "10px"
                                    }}
                                    target="__blank"
                                    href={this.props.author?.link}>
                                    {item?.author}
                                </a>
                            </span>
                            <span className="align-items-start justify-content-end blog-pub-date"
                                style={{
                                    flex: 2,
                                    textAlign: "right"
                                }}>
                                {item?.pubDate && moment(item?.pubDate).format('LL')}
                            </span>
                        </p>
                        <h4 className="card-title blog-title" style={{
                            cursor: "pointer"
                        }} onClick={() => window.open(item?.link, "__blank")}>
                            {item?.title.replace(/(&amp;)/ig, " & ")}
                        </h4>
                        <p className="card-text blog-content">{item?.description.replace(/(<([^>]+)>)/ig, "").replace(/\"/ig, "").replace(/(&amp;)/ig, " & ").split(/\n/).slice(2).join("\n")}</p>
                        {/* <hr style={{ maxHeight: "5px", margin: "0px 0px 0px 5px" }} /> */}
                        <button className="readBtn" onClick={() => window.open(item?.link, "__blank")} target="__blank">Devamını Oku</button>
                    </div>
                </div>
            </div>
        )
    }
    renderLatestBlogsSection() {
        return (
            <section className="blog" id="blog">
                <h2 className="blog-header"><img src="assets/svg/medium-sqr.svg" width="40px" height="37px" style={{ marginRight: "12px" }} /> <img src="assets/svg/medium-long.svg" width="150px" height="37px" style={{ marginRight: "12px" }} /></h2>
                <div className="container-fluid top-container">
                    <div className="blog-container">
                        <div className="container">
                            <div className="row">
                                {(this.props.posts || []).map((item, index) => this.renderOnePost(item))}
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        )
    }

    render() {
        return (
            <div>
                {this.renderBackgroundImageSection()}
                {this.renderAboutSection()}
                {this.renderLatestCoursesSection()}
                {this.state.loaded && this.renderInBetweenSection()}
                {this.renderLatestBlogsSection()}
                {this.renderToastSection()}
            </div>
        )
    }
}


export default connect((state) => ({
    posts: state?.blogs?.posts,
    author: state?.blogs?.author
}), {
    getPosts,
    toggleSendMessageModal
})(HomeScreen)