class InstagramScreen extends Component {

    constructor(props) {
        super(props);
    }
    componentDidMount() {

    }
    renderSocialItem(config = {
        className: "facebook",
        logoText: "Facebook",
        icon: "assets/svg/social/facebook.svg",
        link: "https://www.facebook.com/cangokceaslan",
        name: "Can Gökçeaslan",
        description: "Bu sayfayı ziyaret ederek Facebook sayfama ulaşabilirsin"
    }) {
        return (
            <div className="col-lg-4 col-md-6 col-12 item" onClick={() => {
                window.open(config?.link, "__blank")
            }}>
                <div className="social-item">
                    <div className={`logo-container ${config?.className}`}>
                        <div style={{ display: "flex" }}>
                            <img src={config?.icon} style={{ marginRight: "6px" }} width="30px" height="30px" height="auto" />
                            <h3>{config?.logoText}</h3>
                        </div>
                        <div className="profilePhotoContainer">
                            <img src="assets/jpg/social.jpg" width="100px" height="100px" alt="Can Gökçeaslan'ın ana resimi" />
                        </div>
                    </div>
                    <div className="name">
                        {config?.name}
                    </div>
                    <div className="description">
                        {config?.description}
                    </div>
                </div>
            </div>
        )
    }
    render() {
        return (
            <div className="social">
                <div className="container">
                    <div className="row">
                        {data.map((config, i) => this.renderSocialItem(config))}
                    </div>
                </div>
            </div>
        )
    }
}


