import React, { Component } from 'react';
import { connect } from 'react-redux';
import { TwitchEmbed, TwitchChat, TwitchClip, TwitchPlayer } from 'react-twitch-embed';
import { setResizeFunction } from '../../modules/static/window';
import responsive from '../../contants/responsive';
class Twitch extends Component {
    state = {
        innerWidth: window.innerWidth
    }
    setResizer() {
        this.props.setResizeFunction((function (e) {
            this.setState({
                innerWidth: window.innerWidth,
                innerHeight: window.innerWidth,
            })
        }).bind(this))
    }
    componentDidMount() {
        //this.setResizer()
    }
    render() {
        return (
            <div>
                <div className="justify-content-center" style={{ height: "100%" }}>
                    <TwitchEmbed
                        channel="cangokceaslan"
                        id="cangokceaslan"
                        theme="dark"
                        allowfullscreen={true}
                        theme="dark"
                        width="100%"
                        style={{
                            height: this.state.innerWidth <= responsive.medium ? "75vh" : "55vh"
                        }}
                        onVideoPause={() => console.log(':(')}
                    />
                </div>
            </div>
        )
    }
}
export default connect(state => ({
    win: state.window
}), { setResizeFunction })(Twitch)