import React, { Component } from 'react';
import { TwitchEmbed, TwitchChat, TwitchClip, TwitchPlayer } from 'react-twitch-embed';
import globalStyles from '../../../common/style';
export class TwitchChatScreen extends Component {
    render() {
        return <div className="align-items-center" style={{
            width: "100vw",
            backgroundColor: globalStyles.primaryColor,
            height: "100vh",
            display: "flex"
        }}>
            <div style={{
                width: "25vw",
                height: "calc(100vh)"
            }}>
                <TwitchChat
                    style={{
                        height: "inherit",
                        paddingTop: "80px"
                    }}
                    channel="cangokceaslan"
                    width="100%"
                    height="calc(100vh - 80px)"
                    theme="dark"
                    allowfullscreen={true}
                />
            </div>

        </div>
    }
}
export default class TwitchScreen extends Component {
    state = {
        loaded: false
    }
    componentDidMount() {
        setTimeout((() => {
            this.setState({
                loaded: true
            })
        }).bind(this), 150)
    }
    render() {
        return (
            <div className="align-items-center" style={{
                width: "100vw",
                height: "100%"
            }}>
                <div style={{
                    width: "100vw",
                    paddingTop: "80px",
                    height: "calc(100vh)"
                }}>
                    {
                        this.state.loaded && <TwitchEmbed
                            channel="cangokceaslan"
                            id="cangokceaslan"
                            theme="dark"
                            allowfullscreen={true}
                            theme="dark"
                            width="100%"
                            height="100%"
                            onVideoPause={() => console.log(':(')}
                        />
                    }
                </div>
            </div>
        )
    }
}
