import axios from 'axios';
import SavedStorage from './localStorage';
export const SEND_MESSAGE = "CONTACT/SEND_MESSAGE";
export const SEND_MESSAGE_SUCCESS = "CONTACT/SEND_MESSAGE_SUCCESS";
export const SEND_MESSAGE_FAILED = "CONTACT/SEND_MESSAGE_FAILED";

export const sendContact = (payload = {}) => (dispatch, getStore) => {
    dispatch({ type: SEND_MESSAGE })
    return fetch("https://script.google.com/macros/s/AKfycbxn-l-Vbn69FrhfDSyMAY7xZQghiqwSjZl37kOCmG2utWOYaOPoQTpg2SmycKhytR59Xg/exec", {
        "headers": {
            "accept": " */*",
            "accept-language": "tr-TR,tr;q=0.9,en-US;q=0.8,en;q=0.7,no;q=0.6",
            "content-type": "application/x-www-form-urlencoded",
            "sec-ch-ua": "\"Google Chrome\";v=\"93\", \" Not;A Brand\";v=\"99\", \"Chromium\";v=\"93\"",
            "sec-ch-ua-mobile": "?0",
            "sec-ch-ua-platform": "\"Windows\"",
            "sec-fetch-dest": "empty",
            "sec-fetch-mode": "cors",
            "sec-fetch-site": "cross-site",
        },
        "referrer": "https://www.apirequest.io/",
        "referrerPolicy": "strict-origin-when-cross-origin",
        "body": JSON.stringify(payload),
        "method": "POST",
        "mode": "cors",
        "credentials": "omit"
    }).then((response) => {
        return response.json()
    }).then((response) => {
        console.log(response);
        dispatch({
            type: SEND_MESSAGE_SUCCESS, payload: response
        })
    }).catch(function (error) {
        dispatch({ type: SEND_MESSAGE_FAILED, payload: error })
    });
}


const initialState = {
    sent: {

    },
    sentAt: new Date(SavedStorage.getItem("__contactSent")) || undefined,
}

export default (state = initialState, action) => {
    switch (action.type) {
        case SEND_MESSAGE:
            return state;
        case SEND_MESSAGE_SUCCESS:
            SavedStorage.saveItem("__contactSent", new Date().toString());
            return {
                ...state,
                sent: action?.payload,
                sentAt: new Date()
            };
        case SEND_MESSAGE_FAILED:
            return state;
        default:
            return state;
    }
};
