import React, { Component } from 'react';
import _ from 'lodash'
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { setSendMessageModalToggler, toggleSendMessageModal } from '../../modules/static/modals';
import { sendContact } from '../../modules/contact';
import globalStyles from '../../common/style';
class Navbar extends Component {
    hideRoutes = [
        "twitch", "twitch-chat"
    ]
    banners = [
        this.renderUdemyBanner.bind(this),
        this.renderGitHubBanner.bind(this),
        this.renderPatreonBanner.bind(this),
        this.renderYoutubeBanner.bind(this)
    ]
    bannerColors = [
        "#a435f0",
        "rgb(51, 51, 51)",
        "rgb(231, 68, 19)",
        "red"
    ]
    _inputs = {

    }
    state = {
        currentBanner: 0, //|| Math.floor(4 * Math.random()),
        reRender: true,
        hidden: false,
        contactData: {

        },
        diffDays: this.props?.sentAt
            ? parseInt((new Date() - new Date(this.props?.sentAt)) / (1000 * 60 * 60 * 24))
            : 2
    }
    constructor(props) {
        super(props)
        this.toggleTopBanner = this.toggleTopBanner.bind(this)
        this.hideTopBanners = this.hideTopBanners.bind(this);
        this.toggleOffCanvas = this.toggleOffCanvas.bind(this)
        this.props.setSendMessageModalToggler(this.toggleOffCanvas)
    }
    componentDidMount() {
        const hideTopBanners = localStorage.getItem("_hideTopBanners");
        if (!hideTopBanners) {
            this.setState({
                hidden: this.hideRoutes.includes(window.location.pathname.replace(/^\//, ""))
            })
            setInterval(this.toggleTopBanner, 5000)
        } else {
            this.setState({
                hidden: true
            })
        }
    }
    toggleTopBanner() {
        this.setState(state => ({
            currentBanner: (state.currentBanner + 1) % this.banners.length,
            reRender: false
        }), () => {
            this.setState({ reRender: true })
        })
    }
    renderUdemyBanner() {
        return (
            <div className="top-small-banner animate__animated animate__zoomIn">
                <a href="https://www.udemy.com/user/cangokceaslan" target="_blank">
                    <span className="iconContainer" style={{
                        width: "80px !important",
                        minWidth: "79px !important"
                    }}>
                        <img src="assets/svg/udemy.svg" width="95px" height="25px" />
                    </span>
                    <span className="hiddenTextContainer">
                        Udemy
                    </span>
                    <span className="textContainer">
                        eğitimlerim burada, tıklayarak hemen ulaş !!!
                    </span>
                </a>
            </div>
        )
    }
    renderPatreonBanner() {
        return (
            <div className="top-small-banner animate__animated animate__zoomIn" style={{
                backgroundColor: "#E74413"
            }}>
                <a href="https://www.patreon.com/cangokceaslan" target="_blank">
                    <span className="iconContainer" style={{ width: "130px" }}>
                        <img src="assets/svg/patreon.svg" width="85px" height="20px" />
                    </span>
                    <span className="hiddenTextContainer">
                        Patreon
                    </span>
                    <span className="textContainer">
                        sayfama ulaşmak için hemen tıkla !!!
                    </span>
                </a>
            </div>
        )
    }
    renderGitHubBanner() {
        return (
            <div className="top-small-banner animate__animated animate__zoomIn" style={{
                backgroundColor: "#333",
                color: "white"
            }}>
                <a href="https://www.github.com/cangokceaslan" target="_blank">
                    <span className="iconContainer" style={{ width: "100px" }}>
                        <img src="assets/svg/github.svg" width="60px" height="17px" />
                    </span>
                    <span className="hiddenTextContainer">
                        GitHub
                    </span>
                    <span className="textContainer">
                        hesabımdaki kodlara hemen ulaş !!!
                    </span>
                </a>
            </div>
        )
    }
    renderYoutubeBanner() {
        return (
            <div className="top-small-banner animate__animated animate__zoomIn" style={{
                backgroundColor: "red"
            }}>
                <a href="https://www.youtube.com/cangokceaslan?sub_confirmation=1" target="_blank">
                    <span className="iconContainer"
                        style={{
                            width: "130px"
                        }}>
                        <img src="assets/svg/youtube.svg" width="83px" height="18px" />
                    </span>
                    <span className="hiddenTextContainer">
                        Youtube
                    </span>
                    <span className="textContainer">
                        kanalıma ulaşmak için hemen tıkla !!!
                    </span>
                </a>
            </div>
        )
    }
    hideTopBanners() {
        this.setState({ hidden: true }, () => {
            localStorage.setItem("_hideTopBanners", "true")
        })
    }
    toggleOffCanvas() {
        this._offCanvas.click()
    }
    calculateDisabled() {
        if (this.state.diffDays < 1) {
            return true;
        }
        return ["name", "surname", "email", "title", "message"].map((key, index) => {
            return this.state.contactData?.[key];
        }).some(el => el === undefined || el === null || el === "");
    }
    setDataOnContactForm(key, value) {
        this.setState(state => ({
            contactData: {
                ...state?.contactData,
                [key]: value
            }
        }))
    }
    resetInputs() {
        Object.keys(this._inputs).forEach((key, index) => this._inputs[key].value = "")
    }
    renderOffCanvasGlobalRight() {
        return (
            <div>
                <button style={{ display: "none" }}
                    ref={(offcanvas) => {
                        this._offCanvas = offcanvas
                    }} className="btn btn-primary"
                    type="button"
                    data-bs-toggle="offcanvas"
                    data-bs-target="#offcanvasRight"
                    aria-controls="offcanvasRight">
                    Toggle right offcanvas
                </button>

                <div className="offcanvas offcanvas-end contactCanvas" tabindex="-1" id="offcanvasRight" aria-labelledby="offcanvasRightLabel" style={{ zIndex: 1999 }}>
                    <div className="offcanvas-header">
                        <h5 id="offcanvasRightLabel" style={{ fontWeight: "bold" }}>Bana Ulaş</h5>
                        <button type="button" className="btn-close text-reset" data-bs-dismiss="offcanvas" aria-label="Close"></button>
                    </div>
                    <div className="offcanvas-body">
                        <div className="contact-form-group">
                            <label for="name">İsim (★):</label>
                            <input id="name"
                                ref={(input) => {
                                    this._inputs.name = input
                                }}
                                type="text" onChange={(e) => {
                                    this.setDataOnContactForm("name", e.target.value)
                                }}
                                className="form-control"
                                type="text"
                                placeholder="Bu alana İsmini girmelisin"
                                required />
                            <div className="helper-text">
                                <small id="nameHelpBlock" className="form-text text-muted helper-text">
                                    İsmini girebilirsin
                                </small>
                            </div>
                        </div>
                        <div className="contact-form-group">
                            <label for="surname">Soyisim (★):</label>
                            <input id="surname" type="text"
                                ref={(input) => {
                                    this._inputs.surname = input
                                }}
                                onChange={(e) => {
                                    this.setDataOnContactForm("surname", e.target.value)
                                }}
                                className="form-control"
                                type="text"
                                placeholder="Bu alana Soyismini girmelisin"
                                required
                            />
                            <div className="helper-text">
                                <small id="nameHelpBlock" className="form-text text-muted helper-text">
                                    Soyismini girebilirsin
                                </small>
                            </div>
                        </div>
                        {/* <div className="contact-form-group">
                            <label for="telephone">Telefon:</label>
                            <input id="telephone" type="tel" className="form-control" type="text" placeholder="Default input" />
                            <div className="helper-text">
                                <small id="nameHelpBlock" className="form-text text-muted helper-text">
                                    Telefonunu girebilirsin
                                </small>
                            </div>
                        </div> */}
                        <div className="contact-form-group">
                            <label for="email">Email (★):</label>
                            <input
                                id="email"
                                ref={(input) => {
                                    this._inputs.email = input
                                }}
                                type="email"
                                onChange={(e) => {
                                    this.setDataOnContactForm("email", e.target.value)
                                }} className="form-control"
                                type="text"
                                placeholder="Bu alana Epostanı girmelisin"
                                required
                            />
                            <div className="helper-text">
                                <small id="nameHelpBlock" className="form-text text-muted helper-text">
                                    Email'ini girebilirsin
                                </small>
                            </div>
                        </div>
                        <div className="contact-form-group">
                            <label for="title">Konu başlığı (★):</label>
                            <input id="title"
                                ref={(input) => {
                                    this._inputs.title = input
                                }}
                                type="text"
                                onChange={(e) => {
                                    this.setDataOnContactForm("title", e.target.value)
                                }}
                                className="form-control"
                                type="text"
                                placeholder="Bu alana Konu Başlığını girmelisin"
                                required />
                            <div className="helper-text">
                                <small id="nameHelpBlock" className="form-text text-muted helper-text">
                                    Konu başlığını girebilirsin
                                </small>
                            </div>
                        </div>
                        <div className="contact-form-group">
                            <label for="message">Mesajını Gir (★):</label>
                            <textarea id="message"
                                ref={(input) => {
                                    this._inputs.message = input
                                }}
                                onChange={(e) => {
                                    this.setDataOnContactForm("message", e.target.value)
                                }}
                                type="text"
                                rows="5"
                                className="form-control"
                                type="text"
                                placeholder="Bu alana Mesajı girmelisin"
                                required></textarea>
                            <div className="helper-text">
                                <small id="nameHelpBlock" className="form-text text-muted helper-text">
                                    Konu başlığını girebilirsin
                                </small>
                            </div>
                        </div>
                        <div className="contact-form-group">
                            <label for="attachment">Ek Gir:</label>
                            <input id="attachment"
                                ref={(input) => {
                                    this._inputs.attachment = input
                                }}
                                onChange={(e) => {
                                    this.setDataOnContactForm("attachment", e.target.value)
                                }}
                                type="text"
                                className="form-control"
                                type="text"
                                placeholder="Herhangi bir link girebilirsin" />
                            <div className="helper-text">
                                <small id="nameHelpBlock" className="form-text text-muted helper-text">
                                    Herhangi bir link girebilirsin
                                </small>
                            </div>
                        </div>
                        <div className="contact-form-group">
                            <button className="btn btn-primary btn-send-contact"
                                onClick={(e) => {
                                    this.setState({
                                        disabled: true
                                    })
                                    if (!this.calculateDisabled()) {
                                        setTimeout((() => {
                                            this.toggleOffCanvas()
                                            this.resetInputs()
                                            this.setState({
                                                diffDays: 0
                                            })
                                        }).bind(this), 1000)
                                        this.props.sendContact(this.state.contactData);
                                        /* this.props.sendContact({
                                            ...JSON.parse(`{
                                            "name":"Can",
                                            "surname":"Gökçeaslan",
                                            "telephone":"5444850586",
                                            "email":"cangokceaslanx@gmail.com",
                                            "title":"Test",
                                            "message": "Message",
                                            "attachment":"url",
                                            "createdAt": "2021-10-01T14:00:00.234"
                                        }`)
                                                }) */
                                    }
                                }}
                                disabled={this.calculateDisabled() || this.state?.disabled}>
                                {this.state.diffDays < 1
                                    ? "Günde sadece 1 mesaj gönderebilirsin"
                                    : "Mesajı Gönder"}
                            </button>
                        </div>
                    </div>
                </div>
            </div >
        )
    }
    render() {
        return (
            <section>
                {this.renderOffCanvasGlobalRight()}
                <div className="mainNavbar">
                    <div class="animation-overlay"></div>
                    <nav className="navbar navbar-expand-lg navbar-dark bg-dark">
                        <div className="container">
                            <Link className="navbar-brand" to="/">CAN GÖKÇEASLAN</Link>
                            <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
                                <span className="navbar-toggler-icon"></span>
                            </button>
                            <div className="collapse navbar-collapse collapse-dark" id="navbarSupportedContent">
                                <ul className="navbar-nav ms-auto mb-2 mb-lg-0 justify-content-end">
                                    <li className="nav-item">
                                        <Link className="nav-link active" aria-current="page" to="/">Ana Sayfa</Link>
                                    </li>
                                    <li className="nav-item">
                                        <a className="nav-link active" aria-current="page" href="/#about-section">Hakkımda</a>
                                    </li>
                                    <li className="nav-item">
                                        <a className="nav-link active" aria-current="page" href="/#courses">Eğitimlerim</a>
                                    </li>
                                    <li className="nav-item">
                                        <a className="nav-link active" aria-current="page" href="/#blog">Yazılarım</a>
                                    </li>
                                    <li className="nav-item">
                                        <a role="button" className="nav-link" onClick={(e) => {
                                            //this.toggleOffCanvas()
                                            this.props.toggleSendMessageModal()
                                        }}>
                                            Mesaj Gönder
                                        </a>
                                    </li>
                                    <li className="nav-item dropdown">
                                        <a className="nav-link dropdown-toggle" href="#" id="navbarDropdown" role="button" data-bs-toggle="dropdown" aria-expanded="false">
                                            İletişim
                                        </a>
                                        <ul className="dropdown-menu dropdown-menu-dark social-urls" aria-labelledby="navbarDropdown">
                                            <li>
                                                <a target="__blank" className="dropdown-item" href="https://www.facebook.com/cangokceaslan">
                                                    <img loading="lazy" src="assets/svg/social/facebook.svg" width="22px" height="22px" /> Facebook
                                                </a>
                                            </li>
                                            <li>
                                                <a target="__blank" className="dropdown-item" href="https://www.twitter.com/cangokceaslan">
                                                    <img loading="lazy" src="assets/svg/social/twitter.svg" width="22px" height="22px" /> Twitter
                                                </a>
                                            </li>
                                            <li>

                                                <a target="__blank" className="dropdown-item" href="https://www.instagram.com/cangokceaslan">
                                                    <img loading="lazy" src="assets/svg/social/instagram.svg" width="22px" height="22px" /> Instagram
                                                </a>
                                            </li>
                                            <li>

                                                <a target="__blank" className="dropdown-item" href="https://www.udemy.com/user/cangokceaslan">
                                                    <img loading="lazy" src="assets/svg/social/udemy.png" width="22px" height="22px" /> Udemy
                                                </a>
                                            </li>
                                            <li>

                                                <a target="__blank" className="dropdown-item" href="https://www.github.com/cangokceaslan">
                                                    <img loading="lazy" src="assets/svg/social/github.svg" width="22px" height="22px" /> GitHub
                                                </a>
                                            </li>
                                            <li>

                                                <a target="__blank" className="dropdown-item" href="https://www.linkedin.com/in/cangokceaslan">
                                                    <img loading="lazy" src="assets/svg/social/linkedin.svg" width="22px" height="22px" /> Linkedin
                                                </a>
                                            </li>

                                            <li className="divider"><hr className="dropdown-divider" /></li>
                                            <li>
                                                <Link className="dropdown-item" to="/sosyal">
                                                    Tüm Linkler
                                                </Link>
                                            </li>
                                        </ul>
                                    </li>
                                    <li className="nav-item" style={{ display: "flex", alignItems: "center", justifyContent: "center", flex: 1 }}>
                                        <a role="button" target="__blank" href="https://discord.gg/stNxrjtSgD" className="nav-link" style={{
                                            backgroundColor: "#6665d2",
                                            minWidth: "150px",
                                            width: "100%",
                                            textAlign: "left",
                                            borderRadius: "120px",
                                            overflow: "hidden",
                                            textOverflow: "ellipsis",
                                            fontWeight: "600",
                                            fontSize: "15px",
                                            flex: 1,
                                            paddingLeft: "15px",
                                            letterSpacing: "-0.25px",
                                            paddingRight: "15px"
                                        }}>
                                            <img height="20px" width="auto" style={{
                                                marginLeft: "2.5px",
                                                marginRight: "2.5px"
                                            }} src="assets/svg/discord.svg" /> Discord Sunucusu
                                        </a>
                                    </li>
                                </ul>
                            </div>
                        </div>
                    </nav>
                </div>
                {!this.state.hidden
                    && <div style={{
                        backgroundColor: this.bannerColors[this.state.currentBanner],
                        position: "absolute",
                        top: "85px",
                        height: "45px",
                        width: "100vw"
                    }}></div>
                }
                {!this.state.hidden && <div style={{
                    position: "absolute",
                    top: "85px",
                    right: "0px",
                    width: "100vw",
                    zIndex: 996,
                    height: "45px",
                    width: "45px",
                    display: "flex",
                    alignItems: "center",
                    justifyContent: "center"
                }}>
                    <button onClick={this.hideTopBanners} class="closeButton btn-close btn-close-black"></button>
                </div>
                }
                {!this.state.hidden && this.banners[this.state.currentBanner]()}
            </section>
        )
    }
}


export default connect(state => ({
    modals: state.modals,
    sentAt: state.contact?.sentAt
}), {
    setSendMessageModalToggler,
    toggleSendMessageModal,
    sendContact
})(Navbar);