import {
    BrowserRouter as Router,
    Switch,
    Route,
    Link
} from "react-router-dom";
import BottomVideo from '../components/BottomVideo';
import Navbar from "../components/Navbar";
import Footer from "../components/Footer";
import HomeScreen from '../screens/MainStack/HomeScreen';
import TwitchScreen, { TwitchChatScreen } from '../screens/MainStack/TwitchScreen';
import SocialScreen from "../screens/MainStack/SocialScreen";
function MainNavigation(props) {
    return (
        <div className="main">
            <Router>
                <Navbar />
                <BottomVideo />
                <Switch>
                    <Route path="/" exact>
                        <HomeScreen />
                    </Route>
                    <Route path="/twitch">
                        <TwitchScreen />
                    </Route>
                    <Route path="/twitch-chat">
                        <TwitchChatScreen />
                    </Route>
                    <Route path="/sosyal">
                        <SocialScreen />
                    </Route>
                    <Route>
                        <HomeScreen />
                    </Route>
                </Switch>
                <Footer />
            </Router>
        </div>

    )
}


export default MainNavigation;