function StarValue(props) {
    const value = props?.value;
    let calculatedValue = value;
    let isHalf = false;
    if (value % 1 < 0.25) {
        calculatedValue = Math.floor(value);
    } else if (value % 1 > 0.75) {
        calculatedValue = Math.ceil(value);
    } else {
        calculatedValue = Math.floor(value);
        isHalf = true;
    }
    const fullFilled = [];
    for (let c = 0; c < calculatedValue; c++) {
        fullFilled.push(<i className="fas fa-star course-star"></i>)
    }
    const unFilled = [];
    for (let c = 0; c < ((5 - (isHalf === true ? 1 : 0)) - calculatedValue); c++) {
        unFilled.push(<i class="far fa-star course-star"></i>)
    }
    const halfFilled = [];
    for (let c = 0; c < (isHalf === true ? 1 : 0); c++) {
        halfFilled.push(<i class="fas fa-star-half-alt"></i>)
    }
    return (
        <span>
            {[...fullFilled, ...halfFilled, ...unFilled]}
        </span>
    )
}
export default StarValue;