export const RESIZE_FUNCTION_DEFINE = "WINDOW/RESIZE_FUNCTION_DEFINE";

export const setResizeFunction = (payload) => (dispatch) => {
    dispatch({ type: RESIZE_FUNCTION_DEFINE, payload })
}

const initialState = {
    resize: []
}
export default (state = initialState, action) => {
    switch (action.type) {
        case RESIZE_FUNCTION_DEFINE:
            return {
                ...state,
                resize: state?.resize.concat(action?.payload)
            }
        default:
            return state;
    }
};
